* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  z-index: -1;
  display: flex;
  position: absolute;
}

section {
  width: 100vw;
  z-index: 1;
}

img {
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
}

.container {
  max-width: 35%;
  opacity: .85;
  background-color: #ffffffe8;
  border: 2.5px solid #476d84;
  margin: 10vh auto 0;
}

#display {
  text-align: right;
  height: 70px;
  background-color: #ffffffe8;
  padding: 16px 8px;
  font-size: 25px;
  line-height: 70px;
}

.buttons {
  border-bottom: 1px solid #999;
  border-left: 1px solid #999;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  display: grid;
}

.buttons > div {
  border-top: 1px solid #999;
  border-right: 1px solid #999;
}

.button {
  text-align: center;
  cursor: pointer;
  border: .5px solid #999;
  font-size: 25px;
  line-height: 100px;
}

#equal {
  color: #fff;
  background-color: #7baac4;
}

.button:hover {
  color: #fff;
  background-color: #f492a5;
  transition: all .4s ease-in-out;
}

